import ElButton from './ElButton'
import ElCity from './city'

const components = [
  ElButton,
  ElCity
]

const install = function (Vue) {
  // 
  if (install.installed) return
  // 
  components.map(component => Vue.use(component))
}

// 
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  //  install Vue.use() 
  install,
  // 
  ElButton,
  ElCity
}
