import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const importVue = function (name) {
  return () => import(`@/doc/${name}.md`)
}

export default new Router ({
  routes: [
    {
      path: '/ElButton',
      name: 'ElButton',
      text: 'button',
      component: importVue('ElButton')
    },
    {
      path: '/city',
      name: 'city',
      text: '',
      component: importVue('ElCity')
    }
  ]
})